.category {
    padding: 72px 0 190px 0;
    position: relative;
    &__header {
        margin-bottom: 103px;
        font-family: Raleway;
        font-style: normal;
        font-weight: normal;
        font-size: 42px;
        line-height: 160%;
        color: var(--c-text);
        text-transform: capitalize;
    }
    &__wrapper {
        display: flex;
        flex-wrap: wrap;
        column-gap: 40px;
        row-gap: 103px;
    }
}
